import MKTypography from "components/MKTypography";

function About() {
  return (
    <>
      <div
        id="about"
        style={{
          justifyContent: "center",
          flexdirection: "column-reverse",
          width: "75%",
          padding: "5px 20px 5px 20px",
          border: "1.5px solid #A0A0A0",
          borderRadius: "10px",
          marginTop: "35px",
          marginBottom: "8px",
          paddingRight: "20px",
          paddingLeft: "30px",
          paddingBottom: "50px",
          marginLeft: "auto",
          marginRight: "auto",
        }}
      >
        <MKTypography
          variant="h3"
          mt={1}
          mb={1}
          sx={({ breakpoints, typography: { size } }) => ({
            [breakpoints.down("md")]: {
              fontSize: size["2xl"],
            },
          })}
          style={{ textAlign: "center" }}
        >
           propos de nous{" "}
        </MKTypography>

        <p>
          Ltat des routes marocaines se dtriore. Et avec le froid qui est bien install, cela
          narrange rien : verglas, inondations. Rsultat, les routes nationales se dgradent, sans
          compter le passage des poids lourds, et lentretien des routes par les collectivits qui
          nest pas optimal. Or cela peut avoir une incidence sur la scurit routire. Alors, pour
          prvenir les risques, nous somme une quipe des tudiants, nous avons dvelopp
          lapplication Jai mal  ma route. Lobjectif est simple : signaler les axes dangereux.
        </p>
      </div>
    </>
  );
}

export default About;
